import {
  Component,
  DoCheck,
  ElementRef,
  EventEmitter,
  Input,
  OnInit,
  Output,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';

import { Action } from './action';
import { ActionConfig } from './action-config';

import { cloneDeep, defaults, isEqual } from 'lodash';

/**
 * List actions component.
 *
 * By default, buttons and kebab have no padding so they may inherit stying from components such as list and toolbar.
 */
@Component({
  encapsulation: ViewEncapsulation.None,
  selector: 'pfng-action',
  template: `<button class="btn btn-default primary-action {{action.styleClass}}" title="{{action.tooltip}}" type="button" *ngFor="let action of config.primaryActions" [disabled]="action.disabled" [ngClass]="{'invisible': action.visible === false}" (click)="handleAction(action)"><div *ngIf="action.template; then showButtonTemplate else showButton"></div><ng-template #showButtonTemplate let-action="action" [ngTemplateOutlet]="action.template" [ngOutletContext]="{ action: action }"></ng-template><ng-template #showButton>{{action.title}}</ng-template></button><ng-template *ngIf="template !== undefined" let-action="action" [ngTemplateOutlet]="template" [ngOutletContext]="{ action: action }"></ng-template><div class="dropdown-kebab-pf pull-right {{config.moreActionsStyleClass}}" dropdown [ngClass]="{'dropdown': !isMoreActionsDropup, 'dropup': isMoreActionsDropup, 'invisible': config.moreActionsVisible === false}" *ngIf="config.moreActions?.length > 0"><button class="btn btn-link dropdown-toggle" type="button" dropdownToggle [ngClass]="{'disabled': config.moreActionsDisabled}" (click)="initMoreActionsDropup($event)"><span class="fa fa-ellipsis-v"></span></button><ul class="dropdown-menu-right dropdown-menu" aria-labelledby="dropdownKebab" *dropdownMenu><li *ngFor="let action of config.moreActions" class="{{action.styleClass}}" [attr.role]="action.separator === true ? 'separator' : 'menuitem'" [ngClass]="{'divider': action.separator === true, 'disabled': action.disabled === true, 'hidden': action.visible === false}"><a *ngIf="action.disabled !== true && action.separator !== true" class="dropdown-item secondary-action" href="javascript:void(0)" title="{{action.tooltip}}" (click)="handleAction(action)">{{action.title}}</a> <a *ngIf="action.disabled === true && action.separator !== true" class="dropdown-item secondary-action" href="javascript:void(0)" title="{{action.tooltip}}" onclick="return false;">{{action.title}}</a></li></ul></div>`
})
export class ActionComponent implements DoCheck, OnInit {
  /**
   * The action config containing component properties
   */
  @Input() config: ActionConfig;

  /**
   * Action template for custom actions
   */
  @Input() template: TemplateRef<any>;

  /**
   * The event emitted when an action has been selected
   */
  @Output('onActionSelect') onActionSelect = new EventEmitter();

  private defaultConfig = {
    moreActionsDisabled: false,
    moreActionsVisible: true
  } as ActionConfig;
  private isMoreActionsDropup: boolean = false;
  private prevConfig: ActionConfig;

  /**
   * The default constructor
   *
   * @param el The element reference for this component
   */
  constructor(private el: ElementRef) {
  }

  // Initialization

  /**
   *  Setup component configuration upon initialization
   */
  ngOnInit(): void {
    this.setupConfig();
  }

  /**
   *  Check if the component config has changed
   */
  ngDoCheck(): void {
    // Do a deep compare on config
    if (!isEqual(this.config, this.prevConfig)) {
      this.setupConfig();
    }
  }

  /**
   * Set up default config
   */
  protected setupConfig(): void {
    if (this.config !== undefined) {
      defaults(this.config, this.defaultConfig);
    } else {
      this.config = cloneDeep(this.defaultConfig);
    }
  }

  // Private

  private handleAction(action: Action): void {
    if (action && action.disabled !== true) {
      this.onActionSelect.emit(action);
    }
  }

  /**
   * Set flag indicating if kebab should be shown as a dropdown or dropup
   *
   * @param $event The MouseEvent triggering this function
   */
  private initMoreActionsDropup($event: MouseEvent): void {
    window.requestAnimationFrame(() => {
      let kebabContainer = this.closest($event.target, '.dropdown-kebab-pf.open', 'pfng-list-actions');
      let listContainer = this.closest(this.el.nativeElement, '.list-pf', 'pfng-list');
      if (kebabContainer === null || listContainer === null) {
        return;
      }

      let dropdownButton = kebabContainer.querySelector('.dropdown-toggle');
      let dropdownMenu =  kebabContainer.querySelector('.dropdown-menu');
      let buttonRect = dropdownButton.getBoundingClientRect();
      let menuRect = dropdownMenu.getBoundingClientRect();
      let menuTop = buttonRect.top - menuRect.height;
      let menuBottom = buttonRect.top + buttonRect.height + menuRect.height;
      let parentRect = listContainer.getBoundingClientRect();

      if ((menuBottom <= parentRect.top + parentRect.height) || (menuTop < parentRect.top)) {
        this.isMoreActionsDropup = false;
      } else {
        this.isMoreActionsDropup = true;
      }
    });
  }

  // Utils

  /**
   * Get the closest ancestor based on given selector
   *
   * @param el The HTML element to start searching for matching ancestor
   * @param selector The selector to match
   * @param stopSelector If this selector is matched, the search is stopped
   * @returns {HTMLElement} The matching HTML element or null if not found
   */
  private closest(el: any, selector: string, stopSelector: string): HTMLElement {
    let retval = null;
    while (el) {
      if (el.matches(selector)) {
        retval = el;
        break;
      } else if (stopSelector && el.matches(stopSelector)) {
        break;
      }
      el = el.parentElement;
    }
    return retval;
  }
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
